@font-face {
font-family: "ubuntu";
src: url("./Ubuntu-M.ttf");
src:
url("./Ubuntu-M.ttf") format("ttf")
}
body {
  color: #2D4B56;
  font-weight: 300;
}
h1 {

}
ul {
  padding: 0;
  margin: 0
}
li {
  list-style-type: none;
}
.section-title {
  font-size: 28px;
  font-family: 'ubuntu', helvetica, sans-serif;
  font-weight: 500;
}
.header {
  padding: 15px;
  text-align: center;
}
.brand {
  color: #FF5050
}
.clear {
  clear: both;
  height: 1px;
}
.title {
  font-family: 'ubuntu', helvetica, sans-serif;
  font-weight: 400;
}
.white {
  color: #fff;
}
.medium {
  font-size: 18px
}
.hero-section {
  background-color: #FF5050;
  height: 640px;
}
.tagline {
  margin-top: 140px;
  font-size: 40px;
  margin-bottom: 36px
}
.hero-img {
  position: relative;
  top: -306px;
  right: -190px;
}
.app-parent {
  width: 100%;
  overflow-x: hidden;
}
.content {
  width: 1100px;
  margin: auto;
  position: relative;
  height: 100%;
}
.left-icon {
  margin-right: 34px;
  font-size: 50px;
  color: #788D94;
}
.description {
  display: inline-block;
  width: 500px;
}
