.tilted-bg {
  margin-top: -42px;
  position: relative;
  height: 700px;
  width: 100%;
  background-color: #647481;
  z-index: 0;
}
.tilted-bg-parent {
  width: 100%;
  height: auto;
  position: absolute;
}
.section-header {
  font-family: 'ubuntu', helvetica, sans-serif;
  font-weight: 400;
  margin-bottom: 10px;
}
.early-access-parent {
  width: 100%;
  padding: 40px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
}
footer {
  background-color: #FF5050;
  padding: 20px;
}
.col-4, .col-3, .col-2, .col-5, .col-6, .col-7, .col-8{
  position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.convert-section {
  float: right;
}
.convert-image {
  float: left;
  width: 420px;
   position: relative;
   z-index: 1
}
.web-section {
  margin-top: -500px
}
.web-content {
  float: left;
  width: 580px;
  margin-left: 40px;
}
.file-picture {
  float:left;
  width: 520px;
  position: relative;
  z-index: 1;
  margin-right: 50px
  }
  .file-content {
    float:right;
    width: 500px;
    margin-top: 80px
  }
  .file-icon{
    top: -30px;
    position: relative
  }

@media (min-width: 768px) {
  .col-4 {
      width: 33%;
      float: left;
  }
}
@media (max-width: 768px) {
  .hero-section {
    height: 770px
  }
  .hero-img {
    right: auto;
    left: -200px;
  }
  .convert-section {
    float: none;
    padding: 20px 20px
  }
  .convert-image {
    float: none;
    width: 100%;
  }
  .web-section {
    margin-top: -600px
  }
  .description {
    width: auto;
  }
  .convert-section h2 {
    margin-top: 40px
  }
  .content {
    width: 100%;
    padding: 35px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  .web-content {
    float: none;
    width: 100%;
    margin-left: 0;
  }
  .file-picture {
    float:none;
    width: 100%;
    position: relative;
    z-index: 1;
    text-align: center;
  }
  .file-content {
    float:none;
    width: 100%;
    margin-top: 0;
    padding: 20px
  }
  .file-icon{
    top: 0;
  }
}



